}
</script>

<style>
.promo { 
  z-index:20000;
  background-color: rgba(255,255,255,.75);
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  text-align:center;
}

.promo-hero {
  width: 81.5%;
  padding: 4.35%;
  background-color: #000;
  margin-top:16px;
}

.promo-holder {
  width: 100%;
